      <div> 
        <p style="padding-bottom: 3%; color:#23263b">貨幣: &nbsp;<a href="#" @click="convertPrice('ntd')" style="text-decoration: none;">
        <img src="https://w.namu.la/s/183004771c3ffdb5d87f9d8bb979006122a3cc153296d4a32caa605333358df9f6633bddee6114e7c9fceaff414f13923837c5c9a1d61ed63f68a7a0557c4bf2ead456c2ad644b7fa67e0248a532633809bf624fdf3debd144ba08bbc90ce60e"
            style="max-width:5%; height:auto;"/>TWD</a> &nbsp;
        <a href="#" @click="convertPrice('won')" style="text-decoration: none;">
        <img src="https://w.namu.la/s/43a07e65f573eb41fffe67ac0d1008fa73b5c7a04a004ff9004ddf0680524c5c5bd8a30c724fd7966bd7d3a2f60d0bd17c3cc159dd41f704f9b6dc188a21346d909e2fd3e4874159f9c886f53bd04334dd33116fccce9476ea98aed7af35b04c"
            style="max-width:5%; height:auto;"/>KRW</a></p>
      </div>

      <ais-search-box style="max-width:100%; height:auto;" placeholder="品牌、品項、品名(如：BYOUR、洗髮、VT+面膜)" />
    </div>
      <div
        style="flex-basis: 20%; display: flex; justify-content: center; align-items:center; flow-wrap: wrap; flex-direction: column; padding-left: 3%;">
        <img src="@/assets/banner.jpg" style="max-width:100%; height:auto;">
        <a href="https://lin.ee/b1eVSK1" target='_blank'><img src="@/assets/line_friend.png"
            style="max-width:100%; height:auto;"></a>
      </div>
    </div>
    <h1 style="padding-bottom: 3%; color:#DB7093">產品圖案或許會與實際品項有異，訂購時，敬請咨詢確認。感謝您。</h1>
    <ais-dynamic-widgets>
      <ais-refinement-list attribute="selling_location"/>
    </ais-dynamic-widgets>
    <div class="search-panel__results"
      style="display: flex; justify-content: center; align-items:center; flow-wrap: wrap; flex-direction: column;">
      <ais-hits :transform-items="transformItems">
        <template v-slot:item="{ item}">
          <div style="flex-basis: 40%;">
            <img v-bind:src="item.PROD_IMAGE" style="max-width:80%; height:auto;">
          </div>

          <div style="flex-basis: 60%;">
