<script>
import algoliasearch from 'algoliasearch/lite';
import 'instantsearch.css/themes/satellite-min.css';
// import Service from '@/services/Service.js'
import { history as historyRouter } from 'instantsearch.js/es/lib/routers';
import { singleIndex as singleIndexMapping } from 'instantsearch.js/es/lib/stateMappings';
// import AppInfiniteHits from './components/InfiniteHits';

export default {
  // components: { AppInfiniteHits },
  
  props: {
    item: Object
  },
  data() {
    return {
      searchInitFlag: false,
      searchClient: algoliasearch(
        'SZOTLIDLJD',
        '5283f978a80eac551ed19eac8cfb6f3d'
      ),
      routing: {
        router: historyRouter(),
        stateMapping: singleIndexMapping('CUSTOMER_SHOONG_REL'),
      },
      currentPriceUnit: 'ntd',
      currentPriceMap:{},
    }
  },
  created() {
  },
  methods: {
    searchFunction(helper) {
      if(this.searchInitFlag == true && helper.state.query.length > 1) {
        helper.search()
      }else {
        if(this.searchInitFlag == false) {
          this.searchInitFlag = true
          helper.search()
        }
      }
    },
    convertPrice(priceUnit) {
      this.currentPriceUnit = priceUnit
    },
    getNTDpriceByWon(prodCd, won, weight, mode) {

      let currentProdCode = this.currentPriceMap[prodCd]
      if(!currentProdCode){
       
        this.currentPriceMap[prodCd] = {won: won, weight: weight, mode: mode}
        // console.log(prodCd,this.currentPriceMap[prodCd])
      }

      this.convertPriceInPriceUnitMap(prodCd)

      
    },
    convertPriceInPriceUnitMap(prodCd) {
      let {won, weight, mode}= this.currentPriceMap[prodCd]
      let exchangeRateNTD = 42 
      /*
      대만단가 계산법 (항공)
      (((입고단가+물류비7*무게g)*대리결제수수료1.01)/환율)*10%

      예시 
      상품가 : 10000원
      물류비1kg : 7000원 
      무게 : 50g
      환율 : 42

      ((10000+7*50)*1.01)/42)*10%
      */

      let stockPrice = parseInt(won)
      if(mode === 2) { //item.STOCK_PRICE_CONTAIN_VAT ==='1'
          stockPrice = stockPrice / 1.10
      }

      //item.selling_location == 'kr'  (Math.round((parseInt(item.STOCK_PRICE) * 1.10) / 10) *10).toLocaleString()
      //item.STOCK_PRICE_CONTAIN_VAT ==='1' (Math.round(((parseInt(item.STOCK_PRICE) / 1.10) * 1.10) / 10) * 10).toLocaleString() 
      if(weight === '' || weight === undefined || !weight || weight === '0' ||  weight == 0){
        this.currentPriceMap[prodCd].displayPrice = '<p>批發價 (供給價格):</p>&nbsp; <h2>' + (Math.round((stockPrice * 1.10) / 10) * 10).toLocaleString() + " won" + '</h2>'
      } else {
        if(this.currentPriceUnit === 'ntd') {
          this.currentPriceMap[prodCd].displayPrice = '<p>空運批發價 (未稅含運):</p>&nbsp; <h2 style="color:#1E90FF">' + 
          (Math.round( (((stockPrice + (7 * weight)) * 1.01 / exchangeRateNTD ) * 1.1))).toLocaleString() + " NTD" + '</h2>'
        } else {
          this.currentPriceMap[prodCd].displayPrice = '<p>批發價 (供給價格):</p>&nbsp; <h2>' + (Math.round((stockPrice * 1.10) / 10) * 10).toLocaleString() + " won" + '</h2>'
        }
      }
    },
    transformItems(items) {
    this.searchInitFlag = false
    return items
  },
  },
  watch: {
    currentPriceUnit() {
      /*
      todo!------------------------------------------------------clear currentPriceMap whenever ais-hit is validated
      */
      let keys = Object.keys(this.currentPriceMap)
      for(var key in keys){
        this.convertPriceInPriceUnitMap(keys[key])
      }
    }
  },
  
}
</script>

